body, html {
    height: 100%;
    background: #110101;
    font-family: 'Roboto', sans-serif;
    overflow: hidden;
}

.slideshow {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;

    .slider {
        width: 100vw;
        height: 100vw;
        z-index: 2;

        * {
            outline: none;
        }

        .item {
            height: 100vh;
            width: 100vw;
            position: relative;
            overflow: hidden;
            border: none;

            .text {
                display: none;
            }

            img {
                min-width: 101%;
                min-height: 101%;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }

    .slick-dots {
        position: fixed;
        z-index: 100;
        width: 40px;
        height: auto;
        bottom: auto;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        left: auto;
        color: #fff;
        display: block;

        li {
            display: block;
            width: 100%;
            height: auto;

            & button {
                position: relative;
                width: 20px;
                height: 15px;
                text-align: center;

                &:before {
                    content: '';
                    background: #fff;
                    color: #fff;
                    height: 2px;
                    width: 20px;
                    border-radius: 0;
                    position: absolute;
                    top: 50%;
                    right: 0;
                    left: auto;
                    transform: translateY(-50%);
                    transition: all .3s ease-in-out;
                    opacity: 0.6;
                }
            }

            &.slick-active {
                button {
                    &:before {
                        width: 40px;
                        opacity: 1;
                    }
                }
            }
        }
    }

    &.slideshow-right {
        left: 0;
        z-index: 1;
        width: 50vw;
        pointer-events: none;

        .slider {
            left: 0;
            position: absolute;
        }
    }
}

.slideshow-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 100;
    font-size: 80px;
    width: 100vw;
    text-align: center;
    color: #fff;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 100;
    pointer-events: none;
    text-transform: uppercase;
    letter-spacing: 20px;
    line-height: 0.8;

    @media (max-width: 767px) {
        font-size: 40px;
    }

}



